.fixed{
  position: fixed;
  width: 100%;
  .header{
    height: 80px;
    width: 100%;
    background-color: transparent;
    position: relative;
    display: flex;
    border-bottom: 1px solid var(--border-color);
    color: white;
    z-index: 99;
    
    .logo{
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: calc((100vw - 1330px) / 2);
      min-width: 124px;
      width: 100%;
      height: 80px;
      img{
        cursor:pointer;
      }
    }

    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-left: 1px solid var(--border-color);
      border-right: 1px solid var(--border-color);

      .language_switcher{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 30px;
        width: 74px;
        height: 40px;
        border: 1px solid var(--border-color);
        border-radius: 500px;
        cursor: pointer;

        button{
          width: 32px;
          height: 32px;
          border-radius: 500px;
          color: var(--white-color);
          z-index: 1;
          transition: 0.3s color;
          cursor: pointer;
        }

        .active {
          color: #341A0C;
        }

        .circle{
          position:absolute;
          border-radius: 500px;
          width: 32px;
          height: 32px;
          background-color: var(--white-color);
          color: #341A0C;
          margin-right: 32px;
          cursor: pointer;
          transition: 0.3s transform;
          
          &.clicked{
            transform: translateX(100%);
          }
        }
      }

      .services_nav{
        padding-right: 64px;
        display: flex;
        align-items: center;
        cursor: pointer;
        @include ibmplex();
        p{
          margin-left: 16px;
        }
      }

    }

    .cta {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: calc((100vw - 1330px) / 2);
      width: 100%;
      height: 80px;
      right: 0px;
      cursor: pointer;
      background: rgba($color: #000000, $alpha: 0.4);
      @include ibmplex();
      transition: 0.3s background-color;

      &:hover{
        background-color:rgba($color: #000000, $alpha: 0.8) ;
      }
    }



  }
  .service_menu_fixed{
    opacity: 0;
    width: 100%;
    height: 120px;
    display: flex;
    justify-content: center;
    .service_box_fixed{
      width: 50%;
      height: 100%;
      color:white;
      display: flex;
      align-items: center;
      position: relative;
      .service_header_fixed{
        display: inline;
      }
    }
    .right{
      .text{
        padding-left: 64px;
      }
    }
    .left{
      .text{
        position:absolute;
        right: 393px
      }
    }
    h1{
        @include dmsans48_500();
        font-size: 32px;
        margin-right: 9.6px;
    }
    h2{
        font-size: 32px;
        @include dmsans48();    
    }
    .notactive{
      background: #ffffffe0;
      .text{     
        color: var(--black-color);
      }
    }
  }
}