.benefits{
    padding-bottom: 200px;
    overflow: hidden;
    -webkit-backface-visibility: hidden; /* add to fix webkit bug jitter */
    -webkit-transform: perspective(1000px); /* add to fix webkit bug jitter */
    .container{
        display: flex;
        flex-wrap:wrap;
        box-shadow: 0px 0px 0px 300px var(--white-color);
        .box{
            width: calc(50% - 2px);
            height: 376px;
            background-color: var(--white-color);
            border: solid 1px #DFDFDF;
            transition: .4s ease-out;
            h1{
                @include dmsans64_500();
            }
            h2{
                @include dmsans64();
            }
            h3{
                @include dmsans32_500();
                display: inline;
            }
            p{
                @include dmsans32();
                display: inline;
            }
            .wrap{
                padding: 64px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: calc(100% - 128px);
            }
        }
        .first,.third,.fifth{
            border-right: solid 1px var(--white-color);
        }
        .third,.fourth,.fifth,.sixth{
            border-top: solid 1px var(--white-color);
        }

        .first{
            border-top:solid 1px var(--white-color);
            border-left:solid 1px var(--white-color);
            span{
                @include gradient();
            }
        }
        .sixth{
            border-right:solid 1px var(--white-color);
            border-bottom:solid 1px var(--white-color);
        }

        .second,.third,.fourth,.fifth{
            background: linear-gradient(to left, var(--white-color) 50%, transparent 50%) right;
            background-size: 200%;
            span{
                @include gradient2();
            }
            .text{
                max-width: 526px;
            }
        }
        .second:hover,.third:hover,.fourth:hover,.fifth:hover{
            background-position: left;
            color:var(--white-color);
            h2{
                color:var(--white-color);
            }
            span{
                -webkit-text-fill-color: unset;
            }
        } 
    }
}