@font-face {
    font-family: 'DMSans-Bold';
    src: url('./fonts/DMSans/DMSans-Bold.ttf');
}
@font-face {
    font-family: 'DMSans-BoldItalic';
    src: url('./fonts/DMSans/DMSans-BoldItalic.ttf');
}
@font-face {
    font-family: 'DMSans-Italic';
    src: url('./fonts/DMSans/DMSans-Italic.ttf');
}
@font-face {
    font-family: 'DMSans-Medium';
    src: url('./fonts/DMSans/DMSans-Medium.ttf');
}
@font-face {
    font-family: 'DMSans-MediumItalic';
    src: url('./fonts/DMSans/DMSans-MediumItalic.ttf');
}
@font-face {
    font-family: 'DMSans-Regular';
    src: url('./fonts/DMSans/DMSans-Regular.ttf');
}

@font-face {
    font-family: 'IBM_Plex_Sans-Regular';
    src: url('./fonts/IBM_Plex_Sans/IBMPlexSans-Regular.ttf');
}

@mixin dmsans14{
    font-family: 'DMSans-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
}

