@mixin gradient {
    background: linear-gradient(90deg, var(--brown-color) 0%, var(--bronze-color) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    //background-clip: text;
    //text-fill-color: transparent;
}

@mixin gradient2 {
    background: linear-gradient(79.11deg, #4A2110 -92.79%, #CE7B38 83.87%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    //background-clip: text;
    //text-fill-color: transparent;
}