.where{
    min-height: 1727px;
    position: relative;
    overflow: hidden;
    margin-top: -1px;
    //background-color: white;
    .circle{
        position: absolute;
        z-index: -1;
        left: calc((100vw - 1331px) / 2);
        transform: translateY(200px);
        width: 1331px;
        height: 1331px;
        background-color: transparent;
        border-radius: 100%;
        box-shadow: 0px 0px 0px 3000px var(--white-color);
    }
    .innercircle{
        position: absolute;
        z-index: -1;
        left: calc((100vw - 1013px) / 2);
        transform: translateY(359px);
        width: 1013px;
        height: 1013px;
        background-color: var(--white-color);
        border-radius: 100%;
    }
    .reveal{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 685px;
        .first{
            max-width: 907px;
            text-align: center;
            padding-bottom: 91px;
            p{
                @include dmsans92();
            }
            span{
                @include dmsans92_500();
                @include gradient2();
            }
        }
        .second{
            display: flex;
            background-image: url("./images/bg/btn-bg.png");
            .box{
                transition: .7s ease-out;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 320px;
                height: 96px;
                background: #ffffffe0;
                color: var(--black-color);
                p{
                    mix-blend-mode: none;
                    @include dmsans24();
                }
            }
            .active{
                background: none;
                color:var(--white-color);
            }
        }
    }
}