.reasons{
    background-color: var(--white-color);
    min-height: 1319px;
    .container{
        padding-top: 200px;
        .headline{
            h1{
                @include dmsans92();
                padding-bottom: 48px;
            }
            p{
                @include dmsans28();
                padding-bottom: 200px;
            }
            span{
                @include gradient();
            }
        }
        .bulletpoints{
            width: 100%;
            p{
                transition: 0.4s ease-out;
                @include dmsans32();
            }
            .row{
                height: 112px;
                width: 100%;
                border-top: 1px solid #DFDFDF;
                display: flex;
                align-items: center;
                .dot{
                    transition: 0.6s ease-in-out;
                    width: 21px;
                    height: 21px;
                    border-radius: 100%;
                    background: linear-gradient(90deg, var(--brown-color), var(--bronze-color));
                    opacity: 0;
                    position: absolute;
                }
            }

            .row:hover{
                p{
                    transform: translateX(33px);
                }
                .dot{
                    opacity: 100;
                }
            }

            .last{
                border-bottom: 1px solid #DFDFDF;
            } 
        }
    }
}