.partners{
    height: 278px;
    display: flex;
    align-items: center;
    background-color: var(--white-color);
    border-top: solid 1px #C4C4C4;
    .logos_wrap{
        position: relative;
        overflow: hidden;
        transform: translate3d(0,0,0);
        height: 116px;
        width: 100%;
        .logos{
            width: 4675px;
            background: url("./images/bg/loga_bw.png");
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            transform: translate3d(0, 0, 0);
            animation: moveSlideshow 18s linear infinite;
        }
    }
}

@keyframes moveSlideshow {
    100% { 
      transform: translateX(-50.8%);  
    }
}