.quote{
    min-height: 100vh;
    background-color: transparent;
    .container{
        display: flex;
        color: white;
        border: 0 1px 0 1px solid var(--border-color);
        h1{
            display: inline;
            @include dmsans92_500();
        }
        h2{
            display: inline;
            @include dmsans92();
        }
        p{
            @include dmsans28();
            max-width: 506px;
        }
        .fifty{
            width: 50%;
        }
        .left{
            padding-top: 200px;
            p{
                padding-left: 91px;
            }
        }
        .right{
            min-height: 100vh;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding-bottom: 152px;
            border-left: 1px solid var(--border-color);
            p{
                padding-left: 102px;
            }
        }
    }
}