.hero{
    position: relative;
    width: 100%;
    height: 604px;
    overflow: hidden;
    -webkit-backface-visibility: hidden; /* add to fix webkit bug jitter */
    -webkit-transform: perspective(1000px); /* add to fix webkit bug jitter */
    //background-color: var(--white-color);
    .container{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-shadow: 0px 0px 0px 400px var(--white-color);
        .big_text{
            background-color: var(--white-color);
            box-shadow: 0px 0px 0px 400px var(--white-color);
            @include dmsans92();
            span{
                @include gradient();
            }
        }
        .fifty{
            overflow: hidden;
            flex: 50%;
            height: 100%;
            display: flex;
            align-items: center;
            
        }
        .right{
            justify-content: center;
        }

        .numbers{
            background-color: transparent;
            box-shadow: 0px 0px 0px 300px var(--white-color);
            overflow: hidden;
            img{
                margin-right: 40px;
                
            }
            .first{
                display: flex;
                padding-bottom: 138px;
                overflow: hidden;
                img{
                    box-shadow: 0px 0px 0px 300px var(--white-color);
                }
            }
            .second{
                display: flex;
                //box-shadow: 0px 0px 0px 300px #FFFFFF;
                padding-left: 5px;
                padding-top: 1px;
                overflow: hidden;
                img{
                    box-shadow: 0px 0px 0px 300px var(--white-color);
                }
            }
            @include dmsans24_500();
            
        }
    }
}